// @flow

/**
 * CurrentUsersActions.js -            
 * @module actions/CurrentUsersActions
 * @requires module:config
 * @requires module:constants/CurrentUsers
 * @requires module:common/index
 * @requires module:api/index
 * */

import {
    SET_CURRENT_USERS,
    SET_AVG_VIEWTIME,
    SET_RECIRCULATION,
} from '../constants/CurrentUsers'
import {interval, getRecirculation} from '../common'
import {POLLING_INTERVAL, dataProviderTypes} from '../config'
import {getAPI} from '../api'
const api = getAPI(dataProviderTypes.LINKPULSE, (api) => api);

/**
 * @memberof actions/CurrentUsersActions
 * */
export function setCurrentUsers(count: number): CurrentUsersAction {
    return {
        type: SET_CURRENT_USERS,
        count: count
    }
}

/**
 * @memberof actions/CurrentUsersActions
 * */
export function setAvgViewtime(viewtime: number): AvgViewtimeAction {
    return {
        type: SET_AVG_VIEWTIME,
        viewtime: viewtime
    }
}

/**
 * @memberof actions/CurrentUsersActions
 * */
export function setRecirculation(recirculation: number): RecirculationAction {
    return {
        type: SET_RECIRCULATION,
        recirculation: recirculation
    }
}

/**
 *      API  
 * @memberof actions/CurrentUsersActions
 * */
export function fetchCurrentUsers() {
    return (dispatch: any) => {
        interval(() => {
            api.fetchCurrentUsers().then((count: number) => {
                dispatch(setCurrentUsers(count))
            }).catch((error) => {
                console.log(error)
            })
        }, POLLING_INTERVAL, true, 'fetchCurrentUsers')
    }
}

/**
 *      API  
 * @memberof actions/CurrentUsersActions
 * */
export function fetchAvgViewtime() {
    return (dispatch: any) => {
        interval(() => {
            api.fetchAvgViewtime().then((viewtime: number) => {
                dispatch(setAvgViewtime(viewtime))
            }).catch((error) => {
                console.log(error)
            })
        }, POLLING_INTERVAL, true, 'fetchAvgViewtime')
    }
}

/**
 *     API  
 * @memberof actions/CurrentUsersActions
 * */
export function fetchRecirculation() {
    return (dispatch: any, getState: any) => {
        interval(() => {
            api.fetchClicks().then((clicks: number) => {
                dispatch(setRecirculation(getRecirculation(getState().currentUsers.count, clicks)))
            }).catch((error) => {
                console.log(error)
            })
        }, POLLING_INTERVAL, true, 'fetchRecirculation')
    }
}



