// @flow

/**
 * Dashboard.js -    
 * @module containers/Dashboard
 * @requires module:components/Nav
 * @requires module:components/Counter
 * @requires module:components/List
 * */

import React, {Component} from 'react'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import ReactTooltip from 'react-tooltip'

import {Layout} from '../../components/Layout/Layout'
import {Counter} from '../../components/Counter/Counter'
import {List} from '../../components/List/List'
import {Chart} from '../../containers/Chart/Chart'

import {clearAllIntervals} from '../../common'

import * as currentUsersActions from '../../actions/CurrentUsersActions'
import * as topArticlesActions from '../../actions/TopArticlesActions'
import * as usersTimelineActions from '../../actions/UsersTimelineActions'
import * as topReferersActions from '../../actions/TopReferersActions'
import * as devicesActions from '../../actions/DevicesActions'

import './Dashboard.css'

/**
 * @memberof containers/Dashboard
 * */
type Timeline = {
    date: string,
    today: number,
    weekAgo: number,
}

/**
 * @memberof containers/Dashboard
 * */
type Props = {
    currentUsersActions(): {
        fetchCurrentUsers(): any,
        fetchAvgViewtime(): any,
        fetchRecirculation(): any,
    },
    topArticlesActions(): {
        fetchTopArticles(): any,
    },
    usersTimelineActions(): {
        fetchUsersTimeline(): any,
    },
    topReferersActions(): {
        fetchTopReferers(): any,
    },
    devicesActions(): {
        fetchDevices(): any,
    },
    currentUsers: {
        count: number,
        viewtime: number,
        recirculation: number,
    },
    topArticles: {
        topArticles: [{
            value: number,
            title: string,
            url: string,
            percent: number
        }]
    },
    usersTimeline: {
        timeline: Timeline[],
    },
    topReferers: {
        topReferers: [{
            value: number,
            title: string,
            url: string,
            percent: number
        }]
    },
    devices: [{
        value: number,
        title: string,
        percent: number,
        url: string,
    }],
}

const info = {
    realtime: '     ',
    viewtime: '     ',
    recirculation: '         ',
}

const topArticlesSortIcons: string[] = ['visibility', 'watch_later']
const topArticlesSortKeys: string[] = ['pageviews', 'viewtime']

/**
 * @namespace Dashboard
 * */
export class Dashboard extends Component<Props, void> {
    /**
     * */
    static props:Props
    
    /**
     * */
    static ARTICLES_LIMIT: number = 100
    
    /**
     * */
    static REFERERS_LIMIT: number = 10
    
    /**
     * */
    constructor() {
        super()
        this.topArticlesSort = this.topArticlesSort.bind(this)
    }
    
    /**
     *    
     * */
    componentDidMount() {
        const {fetchCurrentUsers, fetchAvgViewtime, fetchRecirculation} = this.props.currentUsersActions
        const {fetchTopArticles} = this.props.topArticlesActions
        const {fetchUsersTimeline} = this.props.usersTimelineActions
        const {fetchTopReferers} = this.props.topReferersActions
        const {fetchDevices} = this.props.devicesActions
        fetchCurrentUsers()
        fetchAvgViewtime()
        fetchRecirculation()
        fetchTopArticles({
            maxArticlesCount: this.constructor.ARTICLES_LIMIT,
        })
        fetchUsersTimeline()
        fetchTopReferers(this.constructor.REFERERS_LIMIT)
        fetchDevices()
    }
    
    /**
     *  
     * */
    componentWillUnmount() {
        clearAllIntervals()
    }
    
    /**
     *     
     * */
    render() {
        const {currentUsers, devices} = this.props
        const {topArticles} = this.props.topArticles
        const {topReferers} = this.props.topReferers
        const {timeline} = this.props.usersTimeline
        
        return (
        <Layout>
            <div className="Dashboard">
                <div className="row">
                    <div className="col-3 p-0 Dashboard__sidebar">
                        <div className="Dashboard__tiles">
                            <div className="Dashboard__tile Dashboard__tile--col-3" data-tip={info.realtime}>
                                <Counter count={currentUsers.count} title=" " theme="blue" data-tip="test"/>
                            </div>
                            <div className="Dashboard__tile Dashboard__tile--col-3" data-tip={info.viewtime}>
                                <Counter count={currentUsers.viewtime} title="  " theme="green"/>
                            </div>
                            <div className="Dashboard__tile Dashboard__tile--col-3" data-tip={info.recirculation}>
                                <Counter count={currentUsers.recirculation} title="% " theme="purple"/>
                            </div>
                        </div>
                        <div className="Dashboard__sidebarList">
                            <List title="" data={topReferers} size="small"/>
                        </div>
                        <div className="Dashboard__sidebarList">
                            <List title="" data={devices} size="small"/>
                        </div>
                    </div>
                    <div className="col-8 Dashboard__content">
                        <Chart data={timeline} title="    "/>
                        <List title=" " data={topArticles} icons={topArticlesSortIcons} hasFilter={true} selectTopCount={10} onSortClick={this.topArticlesSort}/>
                    </div>
                </div>
        
                <ReactTooltip place="right" type="dark" effect="solid"/>
            </div>
        </Layout>
        )
    }
    
    /**
     *   
     * */
    topArticlesSort(type: string) {
        const sort = topArticlesSortKeys[topArticlesSortIcons.indexOf(type)]
        
        this.props.topArticlesActions.fetchTopArticles({
            maxArticlesCount: this.constructor.ARTICLES_LIMIT,
            sort: sort,
        })
    }
}

function mapStateToProps(state) {
    return {
        currentUsers: state.currentUsers,
        topArticles: state.topArticles,
        usersTimeline: state.usersTimeline,
        topReferers: state.topReferers,
        devices: state.devices,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        currentUsersActions: bindActionCreators(currentUsersActions, dispatch),
        topArticlesActions: bindActionCreators(topArticlesActions, dispatch),
        usersTimelineActions: bindActionCreators(usersTimelineActions, dispatch),
        topReferersActions: bindActionCreators(topReferersActions, dispatch),
        devicesActions: bindActionCreators(devicesActions, dispatch),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard)



