// @flow

/**
 * UsersTimelineActions.js -            
 * @module actions/UsersTimelineActions
 * @requires module:config
 * @requires module:constants/UsersTimeline
 * @requires module:common/index
 * @requires module:api/index
 * */

import Promise from 'es6-promise'

import {
SET_USERS,
} from '../constants/UsersTimeline'
import {interval} from '../common'
import {POLLING_INTERVAL, dataProviderTypes} from '../config'
import {getAPI} from '../api'
const api = getAPI(dataProviderTypes.LINKPULSE, (api) => api);

/**
 * @memberof actions/UsersTimelineActions
 * */
type Timeline = {
    date: ?string,
    today?: ?number,
    weekAgo?: ?number,
}

/**
 * @memberof actions/UsersTimelineActions
 * */
type CurrentTimeline = {
    date: ?string,
    total: ?number,
}

/**
 * @memberof actions/UsersTimelineActions
 * */
type UsersTimelineAction = {
    type: string,
    timeline: Timeline[],
}

/**
 * @memberof actions/UsersTimelineActions
 * */
export function setUsersTimeline(timeline: Timeline[]): UsersTimelineAction {
    return {
        type: SET_USERS,
        timeline: timeline,
    }
}

/**
 * @memberof actions/UsersTimelineActions
 * */
export function generateTimelines(length: number, interval: number): Timeline[] {
    const timelines = []
    
    for (let i = 0; i < length; i++) {
        const date = new Date()
        
        date.setHours(0)
        date.setMinutes(interval * i)
        date.setSeconds(0)
        date.setMilliseconds(0)
        
        timelines.push({
            date: date.toISOString()
        })
    }
    
    return timelines
}

/**
 * @memberof actions/UsersTimelineActions
 * */
export function getTransformedData(data: any): Timeline[] {
    const chartInterval = 20
    const timelinesLength = (24 * 60) / chartInterval //   20   24 
    const timelines: Timeline[] = generateTimelines(timelinesLength, chartInterval)
    
    timelines.forEach((item, i) => {
        if (data.weekAgo && data.weekAgo[i]) {
            timelines[i].weekAgo = data.weekAgo[i].total
            timelines[i].date = data.weekAgo[i].date
        }
        
        if (data.today && data.today[i]) {
            timelines[i].today = data.today[i].total
            timelines[i].date = data.today[i].date
        }
    })
    
    return timelines
}

/**
 * @memberof actions/UsersTimelineActions
 * */
export function getArrayFromTimeline(data: Timeline[], key: string = 'weekAgo'): CurrentTimeline[] {
    return data.map((item) => ({
        date: item.date,
        total: item[key],
    }))
}

/**
 * @memberof actions/UsersTimelineActions
 * */
export function hasTimelineCategory(timeline: Timeline[], key: string = 'weekAgo'): boolean {
    return Boolean(timeline.length && typeof timeline[0][key] !== 'undefined')
}

/**
 *        API  
 * @memberof actions/UsersTimelineActions
 * */
export function fetchUsersTimeline(url?: string) {
    return (dispatch: any, getState: any) => {
        interval(() => {
            const data: {
                weekAgo: ?CurrentTimeline[],
                today: ?CurrentTimeline[],
            } = {
                weekAgo: undefined,
                today: undefined,
            }
            
            Promise.resolve()
            .then(() => {
                /*
                *       
                * */
                if (url) {
                    return undefined
                }
                
                /*
                 *        
                 * */
                const timeline = getState().usersTimeline.timeline
                
                if (hasTimelineCategory(timeline)) {
                    return getArrayFromTimeline(timeline)
                } else {
                    return api.fetchUsersTimeline('weekAgo')
                }
            })
            .then((weekAgo?: CurrentTimeline[]) => data.weekAgo = weekAgo)
            .then(() => api.fetchUsersTimeline('today', url))
            .then((today: CurrentTimeline[]) => data.today = today)
            .then(() => dispatch(setUsersTimeline(getTransformedData(data))))
            .catch((error) => console.log(error))
        }, POLLING_INTERVAL, true, 'fetchUsersTimeline')
    }
}



