// @flow

/**
 * TopReferers.js -          
 * @module actions/TopReferers
 * @requires module:config
 * @requires module:constants/TopReferers
 * @requires module:common/index
 * @requires module:api/index
 * */

import {SET_TOP_REFERERS} from '../constants/TopReferers'
import {interval} from '../common'
import {POLLING_INTERVAL, dataProviderTypes} from '../config'
import {getAPI} from '../api'
const api = getAPI(dataProviderTypes.LINKPULSE, (api) => api);

/**
 * @memberof actions/TopReferers
 * */
type TopReferers = [{
    value: number,
    title: string,
    url: string,
    percent: number
}]

/**
 * @memberof actions/TopReferers
 * */
type TopReferersAction = {
    type: string,
    topReferers: TopReferers
}

/**
 * @memberof actions/TopReferers
 * */
export function setTopReferers(topReferers: TopReferers): TopReferersAction {
    return {
        type: SET_TOP_REFERERS,
        topReferers: topReferers
    }
}

/**
 *     API  
 * @memberof actions/TopReferers
 * */
export function fetchTopReferers(maxArticlesCount: number, url?: string = '') {
    return (dispatch: any) => {
        interval(() => {
            api.fetchTopReferers(maxArticlesCount, url).then((topReferers: TopReferers) => {
                dispatch(setTopReferers(topReferers))
            }).catch((error) => {
                console.log(error)
            })
        }, POLLING_INTERVAL, true, 'fetchTopReferers')
    }
}



