// @flow

/**
 * Counter.js -  
 * @module components/Counter
 * */

import React from 'react'

import './Counter.css'

/**
 * @memberof components/Counter
 * */
type Props = {
    title: string,
    count: number,
    theme: string,
};

/**
 * @namespace
 * */
export const Counter = (props: Props) => {
    const {count, title, theme} = props
    
    /**
     * @memberof Counter
     * */
    const themes: string[] = ['green', 'red', 'yellow', 'purple', 'blue']
    
    /**
     * @memberof Counter
     * */
    const themesModifier: string = themes.indexOf(theme) >= 0 ? theme : themes[0]
    
    /**
     *     
     * @memberof Counter
     * @alias render
     * */
    return (
    <div className={`Counter Counter--theme-${themesModifier}`}>
        <div className="Counter__title">{title}</div>
        <div className="Counter__count">{count}</div>
    </div>
    )
}



