// @flow

/**
 * CustomChartTooltip.js -    recharts
 * @module components/CustomChartTooltip
 * */

import React from 'react'
import moment from 'moment'
import '../../../node_modules/moment/locale/ru.js'

import './CustomChartTooltip.css'

moment.locale('ru')

/**
 * @namespace
 * */
export const CustomChartTooltip = (props: any) => {
    const { active, payload = [] } = props
    
    if (active && payload) {
        const { label } = props
    
        /**
         *     
         * @memberof CustomChartTooltip
         * @alias render
         * */
        return (
        <div className="CustomChartTooltip">
            <p className="CustomChartTooltip__label">{moment(label).format('DD MMMM, HH:mm')}</p>
            {payload.map((item, i) => {
                return (
                <p key={i} className="CustomChartTooltip__item">{item.name}: <span className="CustomChartTooltip__value">{item.value}</span></p>
                )
            })}
        </div>
        )
    }
    
    return null
}



