// @flow

/**
 * DevicesActions.js -         
 * @module actions/DevicesActions
 * @requires module:config
 * @requires module:constants/Devices
 * @requires module:common/index
 * @requires module:api/index
 * */

import {SET_DEVICES} from '../constants/Devices'
import {interval} from '../common'
import {POLLING_INTERVAL, dataProviderTypes} from '../config'
import {getAPI} from '../api'
const api = getAPI(dataProviderTypes.LINKPULSE, (api) => api);


/**
 * @memberof actions/Devices
 * */
type Devices = [{
    value: number,
    title: string,
    percent: number,
    url: string,
}]

/**
 * @memberof actions/Devices
 * */
type DevicesAction = {
    type: string,
    devices: Devices
}

/**
 * @memberof actions/Devices
 * */
export function setDevices(devices: Devices): DevicesAction {
    return {
        type: SET_DEVICES,
        devices: devices
    }
}

/**
 *    
 * @memberof actions/Devices
 * */
export function fetchDevices(url?: string = '') {
    return (dispatch: any) => {
        interval(() => {
            api.fetchDevices(url).then((devices: Devices) => {
                dispatch(setDevices(devices))
            }).catch((error) => {
                console.log(error)
            })
        }, POLLING_INTERVAL, true, 'fetchDevices')
    }
}



