// @flow

/**
 * Chart.js -  
 * @module containers/Chart
 * */

import React, {Component} from 'react'
import {AreaChart, Area, XAxis, YAxis, Tooltip} from 'recharts'
import moment from 'moment'

import {CustomChartTooltip} from '../../components/CustomChartTooltip/CustomChartTooltip'
import {LoadingIndicator} from '../../components/LoadingIndicator/LoadingIndicator'

import './Chart.css'

/**
 * @memberof containers/Chart
 * */
type Timeline = {
    date: string,
    today: number,
    weekAgo: number,
}

/**
 * @memberof containers/Chart
 * */
type Props = {
    data: Timeline[],
    title: string,
}

/**
 * @memberof containers/Chart
 * */
type State = {
    width: number,
    height: number,
}

/**
 * @namespace
 * */
export class Chart extends Component<Props, State> {
    /**
     * */
    props: Props
    
    /**
     * */
    state: State
    
    /**
     * */
    ratio: number
    
    /**
     * */
    onResizeBind: Function
    
    /**
     * */
    constructor() {
        super()
        
        this.ratio = 0.3
        this.state = {
            width: 0,
            height: 0,
        }
    
        this.onResizeBind = this.onResize.bind(this)
    }
    
    /**
     * */
    componentDidMount() {
        global.addEventListener('resize', this.onResizeBind)
        this.onResize()
    }
    
    /**
     * */
    componentWillUnmount() {
        global.removeEventListener('resize', this.onResizeBind)
    }
    
    /**
     * */
    onResize() {
        this.setState((prevState) => {
            return {...prevState, width: this.refs.chart.clientWidth, height: this.refs.chart.clientWidth * this.ratio}
        })
    }
    
    /**
     * */
    formatXAxis(tick: string): string {
        return moment(tick).format('HH:mm')
    }
    
    /**
     *     
     * @memberof Chart
     * @alias render
     * */
    render() {
        const {data, title} = this.props
        const hasWeekAgo = Boolean(data.length && typeof data[0].weekAgo !== 'undefined')
        
        const chart = (
            <AreaChart width={this.state.width} height={this.state.height} data={data} margin={ { top: 0, right: 0, left: 0, bottom: 0 } }>
                <XAxis dataKey="date" tickFormatter={this.formatXAxis} tickLine={false} axisLine={false} interval="preserveStartEnd" minTickGap={100} />
                <YAxis width={0} tick={false} tickLine={false} axisLine={false} padding={ { top: 30 } } />
                <Tooltip cursor={ { stroke: '#ececec', strokeWidth: 20 } } content={<CustomChartTooltip />} />
                { (hasWeekAgo) ? (<Area type='monotone' stakId="1" dataKey='weekAgo' name=' ' stroke='#c0b3fb' fill='#c0b3fb' fillOpacity="0.6" />) : null }
                <Area type='monotone' stakId="1" dataKey='today' name='' stroke='#36b8ff80' fill='#36b8ff80' fillOpacity="0.6" />
            </AreaChart>
        )
        
        const loadingIndicator = (
            <div style={ {width: this.state.width, height: this.state.height} }>
                <LoadingIndicator/>
            </div>
        )
        
        return (
            <div ref="chart" className="Chart">
                <div className="Chart__title">{title}</div>
                {(data.length === 0) ? loadingIndicator : chart }
            </div>
        )
    }
}



